.contacts__title {
  flex-basis: 100%;
  .indent();
  margin-left: 9.375%;
  margin-top: 15%;
  margin-bottom: 2%;

  text-align: left;
  font-size: 18px;
  font-weight: 700;
  line-height: 1.6;
}

.contacts__title-icon {
  display: none;
}

.contacts__wrapper {
  display: table;
  width: 100%;
  box-sizing: border-box;
  margin: 0;
  margin-bottom: 9%;
  padding-left: 9%;
  padding-right: 9%;
  font-size: 15px;
}

.contacts__wrapper-row {
  display: table-row;
}

.contacts__text {
  display: table-cell;
  .indent();
  color: @text-color;
  padding-top: 1.5em;
  padding-bottom: 0.5em;
  vertical-align: top;
  text-align: left;
  font-weight: 400;
  line-height: 2;
}

.contacts__text-info {
  max-width: 75%;
  padding-left: 1.125em;
  text-align: left;
  vertical-align: top;
}

.contacts__link {
  color: @text-color;
  text-decoration: none;
  border-bottom: 1px solid @hover-color;
}

.contacts__link-hover,
.contacts__link:hover,
.contacts__link-focus,
.contacts__link:focus {
  color: @hover-color;
}

.contacts__link-active,
.contacts__link:active {
  color: @hover-color;
  opacity: 0.3;
}

.contacts__map {
  position: relative;
  .indent();
  margin-bottom: 12%;

  width: 100%;
  height: 505px;
}

