  .contacts__wrapper-row {
    display: table-row;
  }

  .contacts__text {
    display: table-cell;
    padding-bottom: 2em;
    font-size: 17px;
    line-height: 1.7;
  }

  .contacts__wrapper-row-second .contacts__text,
  .contacts__wrapper-row-second .contacts__text-info {
    padding: 0;
  }

  .contacts__map {
    width: 60%;
    margin-top: -170px;
    float: right;
  }

  .contacts__map::after {
    content: "";
    top: 30%;
  }

  .contacts__map-frame {
    width: 100%;
  }

  .contacts__map-image {
    width: 525px;
    height: 457px;
  }

  .contacts__button {
    float: left;
    font-size: 17px;
  }
